@use "../../../shared/modules/breakpoints.module";
@use "../../../shared/modules/colors.module";
@use "../../../shared/modules/container.module";
@use "../../../shared/modules/units.module";
@use "../../../shared/modules/typography.module";

.input_container {
  position: relative;
  display: flex;
}

.label {
  display: flex;
  align-items: center;
  padding: 0 units.$unit * 2;
  border-radius: 40px 0 0 40px;
  background: colors.$white;
  height: units.$input-button-height;
}

.label_inner {
  user-select: none;
  @include typography.h5 {
    text-transform: uppercase;
    color: colors.$background;
  }
}

.input {
  z-index: 2;
  padding: 0 0 0 units.$unit * 3;
  border-radius: 0 40px 40px 0;
}

.search {
  z-index: 1;
  position: absolute;
  width: units.$unit * 2;
  height: units.$unit * 2;
  right: units.$unit * 2;
  top: 50%;
  transform: translateY(-50%);
}

.hidden {
  display: none;
}

.opened {
  position: absolute;
  background: colors.$background;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.button {
  background: none;
  border: none;
  padding: 0;
}

.icon_search {
  width: 32px;
  height: 32px;
  transition: 0.16s ease-in-out;
  fill: colors.$white;

  path {
    fill: inherit;
  }
}
